.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.label{
  background-color: lightblue;
  border-radius: 5px;
  padding: 5px;
  color: #444;
  margin: 15px;
  display: block;
}

.label:hover{
  background-color: rgb(45, 52, 54);
  color: rgb(87, 8, 8);
  margin: 15px;
  display: block;
}

.label-green{
  background-color: green;
  color: white;
}

form input[type="text"],
form select,
form textarea{
  display: block;
  margin: 10px;
  padding: 5px;
  min-width: 250px;
  max-width: 250px;
}

form select{
  min-width: 263px;
  max-width: 263px;
}

form button,
form input[type = "submit"]{
  display: block;
  float: left;
  margin: 10px;
  padding: 10px;
  cursor: pointer;
  background-color: green;
  color: white;
  border: 1px solid rgb(0, 71, 0);
  transition: all 300ms;
}

form button:hover,
form input[type = "submit"]:hover{
  background-color: #ccc;
  color: #444;
}
